import { createGlobalStyle } from 'styled-components';

export const GlobalStyle = createGlobalStyle`
  *, *::before, *::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    font-family: "Roboto Flex", sans-serif;
  }

  html, body, #root {
    height: 100%;
    font-family: sans-serif;
  }

  #root {
    display: flex;
    flex-direction: column;
  }

  img, picture, video, canvas, svg {
    display: block;
    max-width: 100%;
  }

  img {
    user-select: none;
    pointer-events: none;
  }

  input, button, textarea, select {
    font: inherit;
  }

  a {
    text-decoration: none;
    color: inherit;
  }

  ul, ol {
    list-style: none;
  }

  body {
    background: #000;
  }

  .liver_shake {
    animation: liver_shake 2s ease-in-out infinite;
  }

  .liver_opened {
    animation: liver_opened 0.2s ease-in-out infinite, liver_scale_opened 2s ease-in-out forwards;
  }

  .liver_open_cookie {
    animation: liver_open_cookie 2s ease-in-out forwards;
  }

  @keyframes cookie_side_pulsate {
    0% {
      scale: 0.8;
      opacity: 0.5;
    }
    50% {
      scale: 1;
      opacity: 1;
    }
    100% {
      scale: 0.8;
      opacity: 0.5;
    }
  }

  @keyframes cookie_side_rotate {
    0% {
      rotate: 0deg;
    }
    100% {
      rotate: 360deg;
    }
  }

  @keyframes liver_shake {
    0% {
      transform: skewY(0deg) translateX(0);
    }
    82% {
      transform: skewY(0deg) translateX(0);
    }
    85% {
      transform: skewY(-3deg) translateX(-5px);
    }
    88% {
      transform: skewY(3deg) translateX(5px);
    }
    91% {
      transform: skewY(-3deg) translateX(-5px);
    }
    94% {
      transform: skewY(3deg) translateX(5px);
    }
    97% {
      transform: skewY(0deg) translateX(0);
    }
    100% {
      transform: skewY(0deg) translateX(0);
    }
  }

  @keyframes liver_opened {
    0% {
      transform: skewY(3deg) translateX(5px);
    }
    50% {
      transform: skewY(-3deg) translateX(-5px);
    }
    100% {
      transform: skewY(3deg) translateX(5px);
    }
  }

  @keyframes liver_scale_opened {
    0% {
      scale: 1;
      filter: brightness(1);
    }
    90% {
      scale: 1.2;
      filter: brightness(1.4);
    }
    95% {
      scale: 1.5;
      opacity: 1;
    }
    100% {
      scale: 1;
      opacity: 0;
    }
  }

  @keyframes liver_open_cookie {
    0% {
      opacity: 0;
      scale: 0.8;
      filter: brightness(1.4);
    }
    95% {
      scale: 0.8;
      filter: brightness(1.4);
      opacity: 0;
    }
    100% {
      scale: 1;
      opacity: 1;
    }
  }
`;
