import React from 'react';
import styled from 'styled-components';

const Container = styled.div`
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  min-height: 240px;
  flex: 1 0 0;
  position: relative;
  z-index: 1;
  cursor: pointer;

  &::after {
    content: "";
    display: block;
    width: 420px;
    height: 420px;
    background: url("/fortune-cookie/image/glow_magic.png");
    background-size: cover;
    position: absolute;
    z-index: 2;
    animation: cookie_side_pulsate 3s ease-in-out infinite, cookie_side_rotate 150s linear infinite;
    mix-blend-mode: screen;
  }

  &::before {
    content: "";
    display: block;
    width: 420px;
    height: 420px;
    background: radial-gradient(50% 50% at 50% 50%, #FAEFCE 0%, rgba(238, 204, 145, 0.00) 100%);
    position: absolute;
    z-index: 1;
  }
`;

interface CookieSideProps {
  onClick: () => void;
  isOpened: boolean;
}

export const CookieSide: React.FC<CookieSideProps> = ({ onClick, isOpened }) => {
  return (
    <Container onClick={onClick}>
      <img 
        src="/fortune-cookie/image/closed_liver.png"
        alt="Closed Cookie"
        className={`closed_liver ${isOpened ? 'liver_opened' : 'liver_shake'}`}
        style={{
          width: '240px',
          height: '240px',
          position: 'absolute',
          zIndex: 3
        }}
      />
      <img 
        src="/fortune-cookie/image/open_cookie.png"
        alt="Open Cookie"
        className={`open_cookie ${isOpened ? 'liver_open_cookie' : ''}`}
        style={{
          width: '240px',
          height: '240px',
          position: 'absolute',
          zIndex: 3,
          opacity: 0
        }}
      />
    </Container>
  );
};
