interface StorageData {
  prediction: string;
  purchaseDate: string;
  language: string;
}

export function useStorage() {
  const getItem = (key: keyof StorageData): string | null => {
    return localStorage.getItem(key);
  };

  const setItem = (key: keyof StorageData, value: string): void => {
    localStorage.setItem(key, value);
  };

  const clearPrediction = (): void => {
    localStorage.removeItem('prediction');
    localStorage.removeItem('purchaseDate');
  };

  const isPredictionValid = (): boolean => {
    const purchaseDate = getItem('purchaseDate');
    if (!purchaseDate) return false;

    const purchase = new Date(purchaseDate);
    const now = new Date();

    return purchase.getDate() === now.getDate() &&
           purchase.getMonth() === now.getMonth() &&
           purchase.getFullYear() === now.getFullYear();
  };

  return {
    getItem,
    setItem,
    clearPrediction,
    isPredictionValid
  };
}
