import React from 'react';
import styled from 'styled-components';

const Container = styled.div<{ position: 'left' | 'right' }>`
  display: flex;
  flex-direction: column;

  img {
    width: 12px;
    height: 12px;
    flex: none;

    &:first-child {
      transform: ${props => props.position === 'left' ? 'rotate(0deg)' : 'rotate(90deg)'};
    }

    &:last-child {
      transform: ${props => props.position === 'left' ? 'rotate(-90deg)' : 'rotate(180deg)'};
    }
  }
`;

const Center = styled.div<{ position: 'left' | 'right' }>`
  flex: 1 0 0;
  background: #FAEFCE;
  border-${props => props.position}: 2px solid #692923;
  box-shadow: ${props => props.position === 'left' ? '8px' : '-8px'} 0px 0px 0px rgba(105, 41, 35, 0.10) inset;
`;

interface DetailsBoxDecorProps {
  position: 'left' | 'right';
}

export const DetailsBoxDecor: React.FC<DetailsBoxDecorProps> = ({ position }) => {
  return (
    <Container position={position}>
      {position === 'left' ? (
        <img src="/fortune-cookie/image/details_box_decor.svg" alt="" />
      ) : (
        <img src="/fortune-cookie/image/details_box_decor.svg" alt="" />
      )}
      <Center position={position} />
      {position === 'left' ? (
        <img src="/fortune-cookie/image/details_box_decor.svg" alt="" />
      ) : (
        <img src="/fortune-cookie/image/details_box_decor.svg" alt="" />
      )}
    </Container>
  );
};
