import React from 'react';
import styled from 'styled-components';

const Button = styled.button`
  display: flex;
  max-width: 340px;
  width: 100%;
  background: none;
  border: none;
  filter: drop-shadow(0px 6px 0px rgba(105, 41, 35, 0.16));
  transition: 0.1s;
  position: relative;
  z-index: 2;
  cursor: pointer;

  &:hover {
    transform: translateY(-2px);
    filter: drop-shadow(0px 8px 0px rgba(105, 41, 35, 0.16)) brightness(1.05);
  }

  &:active {
    transform: translateY(2px);
    filter: drop-shadow(0px 4px 0px rgba(105, 41, 35, 0.16)) brightness(0.95);
  }
`;

const ButtonDecor = styled.img<{ position: 'left' | 'right' }>`
  height: 54px;
  width: 12px;
  transform: ${props => props.position === 'right' ? 'scaleX(-1)' : 'none'};
`;

const Content = styled.div`
  display: flex;
  height: 54px;
  padding: 0px 12px;
  justify-content: center;
  align-items: center;
  gap: 8px;
  flex: 1 0 0;
  border-top: 2px solid #692923;
  border-bottom: 2px solid #692923;
  background: #DDA662;
  box-shadow: 0px -8px 0px 0px rgba(105, 41, 35, 0.30) inset, 0px 8px 0px 0px rgba(105, 41, 35, 0.30) inset;

  img {
    width: 32px;
    height: 32px;
    flex: none;
  }
`;

const Text = styled.p`
  color: #692923;
  font-family: "Press Start 2P", serif;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 130%;
  text-transform: uppercase;
  margin-top: 1px;
`;

interface PrimaryButtonProps {
  onClick: () => void;
  text: string;
  icon?: string;
}

export const PrimaryButton: React.FC<PrimaryButtonProps> = ({ onClick, text, icon }) => {
  return (
    <Button onClick={onClick}>
      <ButtonDecor src="/fortune-cookie/image/primary_btn_decor.svg" alt="" position="left" />
      <Content>
        {icon && <img src={`${process.env.PUBLIC_URL}/image/${icon}`} alt="" />}
        <Text>{text}</Text>
      </Content>
      <ButtonDecor src="/fortune-cookie/image/primary_btn_decor.svg" alt="" position="right" />
    </Button>
  );
};
