declare global {
  interface Window {
    Telegram: {
      WebApp: any;
    };
  }
}

const tg = typeof window !== 'undefined' ? window.Telegram?.WebApp : null;

export function useTelegram() {
  const onClose = () => {
    tg?.close();
  };

  const onShare = () => {
    if (tg) {
      tg.switchInlineQuery('Получи своё предсказание на сегодня! 🥠');
    } else {
      console.log('Share clicked (browser mode)');
    }
  };

  const onBuy = async () => {
    console.log('Click processed...');
    return true; // Всегда возвращаем успех
  };

  return {
    onClose,
    onShare,
    onBuy,
    tg,
    user: tg?.initDataUnsafe?.user,
  };
}
